/* modules */
.content {
  padding-top: 54px;
  padding-bottom: 50px;
  &__head {
    .h1, h1, .h2, h2 {
      position: relative;
      padding-left: 47px;
      &::before {
        content: '';
        height: 2px;
        width: 30px;
        position: absolute;
        left: 0;
        top: 17px;
        background: $primary-color;
      }
    }
    .h2, h2 {
      &::before {
        top: 16px;
      }
    }
  }
  &__inside {
    img {
      max-width: 100%;
      display: block;
      margin: 0 auto;
      height: auto;
    }
  }
  &__thumb {
    overflow: hidden;
    position: relative;
    border-radius: 6px;
    margin-bottom: 20px;
    &.--video {
      &::before, &::after {
        content: "\e917";
        color: rgb(173, 0, 159);
        position: absolute;
        right: 14px;
        bottom: 17px;
        z-index: 5;
        pointer-events: none;
        font-family: $font-icomoon;
        font-size: rem(49px);
        line-height: 0.7;
        text-shadow: 0px 6px 25px rgba(173, 0, 159, 0.5);
      }
      &::after {
        content: "\e918";
        margin-left: -1em;
        color: rgb(255, 255, 255);
      }
    }
    &.--threed {
      &::before {
        content: '3D';
        position: absolute;
        font-size: rem(60px);
        color: $add-color-pink;
        line-height: 0.7;
        width: 59px;
        height: 43px;
        padding-right: 135px;
        z-index: 5;
        background: url('data:image/png;base64,') center right no-repeat;
        background-size: 59px 43px;
        right: 15px;
        bottom: 11px;
        font-weight: 900;
      }
    }
    &.--last {
      margin-bottom: 30px;
    }
    img {
      transition: all 0.35s;
      transform: scale(1);
      max-width: 100%;
      height: auto;
      display: block;
      margin: 0 auto;
    }
    &:hover {
      img {
        transform: scale(1.1);
      }
    }
  }
  @include media-breakpoint-up(sm) {
    &__inside {
      padding-left: 45px;
    }
    &__head {
      margin-left: -45px;
    }
    &__thumb {
      margin-bottom: 30px;
      &.--last {
        margin-bottom: 40px;
      }
    }
  }
}

.top-row {
  overflow: hidden;
  position: relative;
  &__wrapper {
    img {
      height: 200px;
      width: 100%;
      object-fit: cover;
    }
  }
  &__title {
    position: absolute;
    left: 0;
    bottom: -10px;
    right: 0;
    z-index: 10;
    display: none;
    .h1, h1 {
      color: $color-white;
      line-height: 1;
      white-space: nowrap;
      font-size: rem(60px);
      line-height: 1.05;
      letter-spacing: 0;
      margin-bottom: 0;
      font-weight: 900;
      @include media-breakpoint-up(md) {
        font-size: rem(100px);
      }
      @include media-breakpoint-up(lg) {
        font-size: rem(130px);
      }
    }
  }
  @include media-breakpoint-up(sm) {
    &__title {
      display: block;
    }
  }
  @include media-breakpoint-up(md) {
    &__title {
      bottom: -18px;
    }
  }
  @include media-breakpoint-up(lg) {
    &__title {
      bottom: -23px;
    }
  }
  @include media-breakpoint-up(xl) {
    &__wrapper {
      img {
        height: 300px;
      }
    }
  }
  @include media-breakpoint-up(xxl) {
    &__wrapper {
      img {
        height: 400px;
      }
    }
  }
}

.apartments {
  &__info {
    margin-bottom: 30px;
    height: 113px;
    font-weight: bold;
    font-size: rem(16px);
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1.3;
    padding: 0 15px;
    background: url('data:image/png;base64,') center center;
    .first {
      padding-bottom: 1px;
    }
    .last {
      font-size: rem(18px);
      padding-left: 15px;
    }
    .b {
      font-size: rem(40px);
      line-height: 1;
      small {
        font-size: rem(25px);
      }
    }
    &.--inside {
      margin-left: -15px;
      margin-right: -15px;
    }
    @include media-breakpoint-up(sm) {
      &.--inside {
        margin-left: -60px;
      }
      .first {
        padding-bottom: 0;
        padding-top: 4px;
      }
      .last {
        font-size: rem(25px);
      }
      .b {
        font-size: rem(50px);
        small {
          font-size: rem(25px);
        }
      }
    }
  }
  &__bottom {
    img {
      margin: 0 auto;
      display: block;
    }
    @include media-breakpoint-up(sm) {}
  }
}